import React,{useEffect,useState} from 'react';
import './App.css';
import firebase from './firebase/Firebase';
import Home from './pages/Home';
import ContactUs from './pages/ContactUs';
import Error from './pages/Error';
import ArticlePage from './pages/ArticlePage';
import Navbar from './components/Navbar';
import Head from "./components/Head";
import Footer from './components/Footer';
import Singlearticle from './pages/Singleaerticle';
import Article from './pages/Article';


import Subscriber from './pages/Subscriber';
import HomeArticle from './pages/HomeArticle';
import Aboutus from './pages/Aboutus';
import GotoTop from './components/GotoTop';
import {HashRouter, Switch, Route} from 'react-router-dom';

function App() {

  const [,setUsersite] = useState(false);

  useEffect(() => {
    firebase.getUserState().then(user => {
        if(user){
            
            
            setUsersite(true);
        }
    });
});







  return (
    <>
    
      
      <Head/>
      <Navbar />
    <HashRouter basename="/">
     

     
          
          

          <Route exact path ="/article/:id" component={Article}/>
          
          
          
          



       
      

       

      
       
       <Route exact path="/" component={Home} />
       <Route exact path="/about-us" component={Aboutus} />

       
       <Route exact path="/article/:id" component={HomeArticle} />

       <Route exact path="/contact-us" component={ContactUs} />
       <Route exact path="/article-page" component={ArticlePage} />
       <Route exact path="/single-article-page/:slug" component={Singlearticle} />


       






       <Route component={Error} />
     
     </HashRouter>
     <Footer />
     
     <GotoTop />
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        
            
    </>
  );
}

export default App;
